import React, { useState, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Link } from 'react-router-dom';
import { Container, Row, Col, Form, Button } from 'react-bootstrap';
import Loader from '../components/Loader';
import Message from '../components/Message';
import Meta from '../components/Meta';
import FormContainer from '../components/FormContainer';
import { register } from '../actions/userActions';

const RegisterScreen = ({ location, history }) => {
  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');
  const [message, setMessage] = useState(null);

  const dispatch = useDispatch();

  const userRegister = useSelector(state => state.userRegister);
  const { loading, error, userInfo } = userRegister;

  const redirect = location.search ? location.search.split('=')[1] : '/';

  useEffect(() => {
    if(userInfo) {
      history.push(redirect)
    }
  }, [userInfo, history, redirect])

  const submitHandler = (e) => {
    e.preventDefault()
    if (password !== confirmPassword) {
      setMessage('Passwords do not match!')
    } else if (!name || !email || !password) {
      setMessage('Name, email and password fields are required!')
    } else {
      dispatch(register(name, email, password))
    }
  }

  return (
    <>
      <Meta title={'Game Master | Register'} />
      <FormContainer>
        <h1 className='text-color-white'>Sign Up</h1>
        {message && <Message variant='danger'>{message}</Message>}
        {error && <Message variant='danger'>{error}</Message>}
        {loading && <Loader />}
        <Form onSubmit={submitHandler}>
          <Form.Group controlId='name' className='mb-1'>
            <Form.Label className='text-color-white'>
              Name
            </Form.Label>
            <Form.Control 
              type='name' 
              placeholder='Enter your name' 
              value={name} 
              onChange={(e) => setName(e.target.value)}
            >
            </Form.Control>
          </Form.Group>

          <Form.Group controlId='email' className='mb-1'>
            <Form.Label className='text-color-white'>
              Email
            </Form.Label>
            <Form.Control 
              type='email' 
              placeholder='Enter your email' 
              value={email} 
              onChange={(e) => setEmail(e.target.value)}
            >
            </Form.Control>
          </Form.Group>

          <Form.Group controlId='password' className='mb-1'>
            <Form.Label className='text-color-white'>
              Password
            </Form.Label>
            <Form.Control 
              type='password' 
              placeholder='Enter your password' 
              value={password} 
              onChange={(e) => setPassword(e.target.value)}
            >
            </Form.Control>
          </Form.Group>

          <Form.Group controlId='confirmPassword' className='mb-1'>
            <Form.Label className='text-color-white'>
              Confirm Password
            </Form.Label>
            <Form.Control 
              type='password' 
              placeholder='Confirm password' 
              value={confirmPassword} 
              onChange={(e) => setConfirmPassword(e.target.value)}
            >
            </Form.Control>
          </Form.Group>

          <Container className='text-center'>
            <Button 
              type='submit'  
              variant='outline-light'
              className='w-50 mt-3 btn-res'
            >
              Sign Up
            </Button>
          </Container>

          <Row className='py-3'>
            <Col className='text-color-white'>
              Already have an account? {' '} 
              <Link 
                to={redirect ? 
                  `/login?redirect=${redirect}` : 
                  '/login'
                }
              > 
                <strong 
                  className='link-hover text-color-white' 
                  style={{textDecoration: 'underline'}}
                > 
                  Sign In
                </strong>
              </Link>
            </Col>
          </Row>
        </Form>
      </FormContainer>
    </>
  );
}

export default RegisterScreen;
